body {
  background: #fff;
}

.container {
  padding: 0 16px;
  margin: 0 auto;
  max-width: 1312px;
  box-sizing: content-box;
}

.title {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.55556;
  color: #ffb81c;
}
.title + .title__text {
  margin: 16px 0 0;
}
.title__text {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.22222;
  letter-spacing: -0.02em;
  color: #1f2a37;
}
.title__text span {
  color: #9da4ae;
}
.title__subtitle {
  margin: 12px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #6c737f;
}

.btn__yellow {
  border: 1px solid #ffb81c;
  border-radius: 24px;
  padding: 9px 12px;
  background: #ffb81c;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #fff;
  transition: all 0.3s;
}
.btn__yellow:hover {
  background: #FFC649;
}

.nav {
  border-bottom: 1px solid #f3f4f6;
  padding: 20px 0;
  background: rgba(17, 17, 31, 0);
  z-index: 5;
}
.nav__mob {
  display: none;
  padding: 0 16px;
}
.nav .container {
  display: flex;
  justify-content: start;
  position: relative;
}
.nav__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 136px;
}
.nav__logo img {
  width: 100%;
}
.nav__list {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 36px;
}
.nav__list a {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #11111f;
  transition: all 0.3s;
}
.nav__list a path {
  transition: all 0.3s;
  stroke: #11111f;
}
.nav__list a:hover {
  color: #9da4ae;
}
.nav__list a:hover path {
  stroke: #9da4ae;
}
.nav__func {
  margin: 0 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__lang {
  position: relative;
  margin: 0 16px 0 0;
  z-index: 10;
}
.nav__lang ul {
  opacity: 0;
  pointer-events: none;
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  border-radius: 32px;
  padding: 8px 12px;
  position: absolute;
  top: calc(100% + 5px);
  right: 0;
  background: #fff;
  border: 1px solid #d2d6db;
  border-radius: 20px;
  z-index: 10;
}
.nav__lang ul a {
  text-align: right;
  display: block;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #11111f;
  transition: all 0.3s;
}
.nav__lang ul a:hover {
  color: #ffb81c;
}
.nav__lang_btn {
  border: 1px solid #d2d6db;
  border-radius: 20px;
  padding: 9px 8px 9px 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #11111f;
  transition: all 0.3s;
  cursor: pointer;
}
.nav__lang_btn:hover {
  color: #9da4ae;
}
.nav__lang.active ul {
  opacity: 1;
  pointer-events: all;
}
.nav__phone {
  width: 40px;
  height: 40px;
  border: 1.11px solid #d2d6db;
  border-radius: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px 0 0;
}
.nav__hum {
  display: none;
}

.header {
  padding: 32px 0 64px;
}
.header .container {
  position: relative;
}
.header__block {
  position: relative;
  z-index: 2;
  mask-image: url("../img/patern.svg");
  mask-size: cover;
  mask-position: left bottom;
  height: 646px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding: 0 32px 96px;
  border-radius: 36px;
  overflow: hidden;
}
.header__block::before {
  content: "";
  display: block;
  width: 2000px;
  height: 1500px;
  position: absolute;
  left: 0;
  bottom: 0;
  background: radial-gradient(ellipse farthest-corner at 50% 50%, rgba(17, 17, 31, 0.8) 0%, rgba(17, 17, 31, 0) 59.3%);
  transform: translate(-50%, 50%);
  z-index: -1;
  pointer-events: none;
}
.header__block h1 {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: -0.02em;
  color: #fff;
  max-width: 630px;
}
.header__block p {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.33333;
  color: #fcfcfd;
  max-width: 630px;
  margin: 24px 0 0;
}
.header__bg {
  position: absolute;
  inset: 0;
  object-fit: cover;
  z-index: -2;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.header .btn__yellow {
  position: absolute;
  bottom: 0;
  left: 16px;
  padding: 16px 28px;
  border-radius: 32px;
  font-size: 18px;
  z-index: 3;
}

.about {
  background: #f9fafb;
}
.about__wrapper {
  padding: 64px 0;
}
.about__wrapper .btn__yellow {
  margin: 24px 0 0;
}
.about .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 64px;
}
.about__rows p {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: -0.02em;
  color: #111927;
  background-size: cover;
}
.about__rows p + p {
  font-weight: 600;
  font-size: 30px;
}
.about__rows_wrapper {
  padding: 36px 32px;
  position: relative;
  z-index: 1;
}
.about__rows_wrapper::before {
  content: "";
  display: block;
  position: absolute;
  background: var(--bg);
  top: 0;
  left: 0;
  height: 100%;
  width: calc(50vw - 32px);
  z-index: -1;
  background-size: cover;
}
.about__rows_wrapper.blc p {
  color: #111927;
}
.about__rows_wrapper.wht p {
  color: #fff;
}
.about__rows_icon {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 160px;
}
.about__rows_icon_bt {
  top: 0;
}

.service {
  padding: 64px 0;
  position: relative;
  overflow: hidden;
}
.service .btn__yellow {
  margin: 24px 0 0;
}
.service__slider {
  margin: 64px 0 0;
  overflow: visible;
}
.service__slide {
  border: 2px solid #e5e7eb;
  border-radius: 24px;
  padding: 24px;
  width: 384px;
  display: flex;
  flex-direction: column;
}
.service__slide_title {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 12px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.5;
  color: #9da4ae;
  margin: 0 0 auto;
}
.service__slide_block {
  border: 1px solid rgba(17, 17, 31, 0.5);
  border-radius: 20px;
  padding: 32px 24px;
  background: #11111f;
  margin: 94px 0 0;
}
.service__slide_block p {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.5;
  color: #fff;
}
.service__slide_block p + p {
  font-weight: 500;
  font-size: 18px;
  margin: 16px 0 0;
  color: #fff;
}
.service__slide_block a {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 8px;
  margin: 32px 0 0;
  width: max-content;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  transition: all 0.3s;
}
.service__slide_block a path {
  transition: all 0.3s;
}
.service__slide_block a:hover {
  color: #ffb81c;
}
.service__slide_block a:hover path {
  stroke: #ffb81c;
}
.service__arrow {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 0 32px;
  margin: 32px 0 0;
  gap: 32px;
}
.service__arrow .swiper-button-next,
.service__arrow .swiper-button-prev {
  position: relative;
  inset: auto;
  transform: none;
  opacity: 1;
  margin: 0;
  width: 56px;
  height: 56px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #9da4ae;
  background: #f9fafb;
}
.service__arrow .swiper-button-next svg,
.service__arrow .swiper-button-prev svg {
  width: 24px;
  height: 24px;
}

.portfolio {
  padding: 64px 0;
  background: #f9fafb;
}
.portfolio .btn__yellow {
  margin: 24px 0 0;
}
.portfolio__wrapper {
  margin: 64px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
}
.portfolio__item {
  max-width: 400px;
  position: relative;
}
.portfolio__item_prev {
  position: relative;
  mask-image: url("../img/portfolio_patern.svg");
  mask-size: 400px;
  mask-position: right top;
  border-radius: 22px;
}
.portfolio__item_prev img {
  width: 100%;
  height: 240px;
  object-fit: cover;
}
.portfolio__item_info {
  padding: 20px 12px 0;
}
.portfolio__item_type {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #11111f;
}
.portfolio__item_name {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.26667;
  color: #111927;
  margin: 4px 0 0;
}
.portfolio__item_status {
  margin: 12px 0 0;
  width: max-content;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 4px;
  padding: 2px 8px 2px 6px;
  background: #f3f4f6;
  border-radius: 16px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  color: #384250;
}
.portfolio__item_status.green {
  background: #ecfdf3;
  color: #027a48;
}
.portfolio__item_map {
  margin: 12px 0 0;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 8px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #4d5761;
}
.portfolio__item:hover .portfolio__link {
  background: #FFC649;
}
.portfolio__link {
  position: absolute;
  top: 0;
  right: 0;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  background: #ffb81c;
  transition: all 0.3s;
}
.portfolio__wht {
  background: #fff;
}
.portfolio__type {
  margin: 12px 0 0;
  border: 1px solid #4d5761;
  border-radius: 16px;
  padding: 2px 8px 2px 6px;
  width: max-content;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  color: #384250;
}

.offer {
  padding: 105px 0;
  background: linear-gradient(139deg, #11111f 20%, #7C5D12 100%);
  text-align: center;
}
.offer .title__text {
  color: #fff;
}
.offer__text {
  margin: 20px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  text-align: center;
  color: #fff;
}
.offer__func {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 0;
  gap: 4px;
}
.offer__phone {
  width: 40px;
  height: 40px;
  border: 1.11px solid #d2d6db;
  border-radius: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer {
  padding: 64px 0 48px;
  background: #11111f;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.footer__logo {
  max-width: 148px;
}
.footer__logo img {
  width: 100%;
}
.footer__text {
  margin: 16px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  color: #f3f4f6;
}
.footer__soc {
  display: flex;
  justify-content: center;
  align-items: start;
  gap: 26px;
}
.footer__soc svg {
  width: 22px;
  height: 22px;
}
.footer__soc a {
  display: block;
}
.footer__list {
  margin: 64px 0 0;
  border-top: 1px solid #f3f4f6;
  padding: 16px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__list a {
  display: block;
  padding: 0 56px;
  position: relative;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.42857;
  color: #f3f4f6;
  transition: all 0.3s;
}
.footer__list a::before {
  content: "";
  display: block;
  width: 1px;
  height: 100%;
  background: #f3f4f6;
  position: absolute;
  top: 0;
  right: 0;
}
.footer__list a:hover {
  color: #ffb81c;
}
.footer__list li:last-child a::before {
  content: none;
}
.footer__copy {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  margin: 32px 0 0;
  color: #4d5761;
}

.about-header {
  padding: 96px 0 64px;
}
.about-header .title__text, .about-header .title__subtitle {
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
.about-header__row {
  margin: 36px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 32px;
}
.about-header__item {
  padding: 20px 36px;
  border-radius: 16px;
  background: #f9fafb;
}
.about-header__item p {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.2;
  letter-spacing: -0.02em;
  text-align: center;
  color: #ffb81c;
}
.about-header__item p + p {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: #111927;
}

.doing {
  padding: 64px 0;
  position: relative;
  background: #f9fafb;
  overflow: hidden;
}
.doing__wrapper {
  max-width: calc(100% - 526px);
}
.doing p {
  margin: 24px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.26667;
  color: #11111f;
}
.doing ul {
  margin: 16px 0 0;
}
.doing ul li {
  position: relative;
  padding: 0 0 0 25px;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #6c737f;
}
.doing ul li::before {
  content: "";
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  background: #6c737f;
  left: 10px;
  top: 11px;
  position: absolute;
}
.doing__bg {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: calc(50vw - 130px);
  border-radius: 48px 0 0 48px;
  object-fit: cover;
}

.goals {
  padding: 64px 0;
}
.goals__wrapper {
  margin: 64px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 32px;
}
.goals__item {
  transition: all 0.3s;
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #f9fafb;
}
.goals__item span {
  border-radius: 32px;
  width: 48px;
  height: 48px;
  background: #ffb81c;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}
.goals__item path {
  transition: all 0.3s;
  stroke: #fff;
}
.goals__item p {
  margin: 36px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.5;
  color: #111927;
}
.goals__item p + p {
  margin: 8px 0 0;
  font-weight: 400;
  font-size: 16px;
  color: #1f2a37;
}
.goals__item:hover {
  background: #ffb81c;
}
.goals__item:hover span {
  background: #fff;
}
.goals__item:hover path {
  stroke: #000;
}

.work {
  padding: 64px 0;
  background: #f9fafb;
}
.work__wrapper {
  margin: 64px 0 0;
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: center;
}
.work__wrapper img {
  height: 90px;
  width: 100%;
  object-fit: contain;
}
.work__wrapper div {
  position: relative;
  width: fit-content;
}
.work__wrapper div::before {
  content: "";
  display: block;
  width: 2px;
  height: 80px;
  position: absolute;
  right: -15px;
  top: 50%;
  transform: translateY(-50%);
  background: #e5e7eb;
}
.work__wrapper div:last-child::before {
  content: none;
}

.team {
  background: #f9fafb;
  padding: 64px 0;
}
.team__wrapper {
  margin: 64px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 107px;
  padding: 0 36px;
}
.team__item {
  position: relative;
}
.team__item::before {
  content: "";
  display: block;
  width: 2px;
  height: 220px;
  position: absolute;
  right: -54px;
  top: 50%;
  transform: translateY(-50%);
  background: #e5e7eb;
}
.team__item_prev {
  position: relative;
  z-index: 2;
}
.team__item_logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  z-index: -1;
}
.team__item_avatar {
  width: 120px;
  height: 120px;
  border-radius: 100%;
  border: 2px solid #000;
  display: block;
  margin: 0 auto;
  object-fit: cover;
}
.team__item p {
  margin: 16px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.5;
  text-align: center;
  color: #111927;
}
.team__item p + p {
  margin: 4px 0 0;
  font-weight: 400;
  font-size: 16px;
  color: #9da4ae;
}
.team__item:last-child::before, .team__item:nth-child(4n)::before {
  content: none;
}

@media (max-width: 1200px) {
  .nav {
    padding: 0;
    border: none;
  }
  .nav .container {
    padding: 16px;
    border-bottom: 1px solid #f3f4f6;
  }
  .nav__list {
    display: none;
    grid-template-columns: 1fr;
    gap: 8px;
    align-items: start;
    border-bottom: 1px solid #eaecf0;
    padding: 24px 16px;
    margin: 0 -16px;
    width: calc(100% + 32px);
  }
  .nav__list a {
    padding: 12px 0;
    justify-content: start;
  }
  .nav__list_mob {
    display: grid;
  }
  .nav__lang_mob, .nav__func_mob {
    display: none;
  }
  .nav__logo {
    position: relative;
    inset: auto;
    transform: none;
  }
  .nav__hum {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 1px solid #d2d6db;
    gap: 4px;
    position: relative;
    flex-direction: column;
    cursor: pointer;
    margin: 0 0 0 4px;
  }
  .nav__hum::before,
  .nav__hum span, .nav__hum::after {
    content: "";
    display: block;
    width: 22px;
    height: 2px;
    background: #11111f;
    border-radius: 2px;
  }
  .nav.active .nav__hum span {
    opacity: 0;
  }
  .nav.active .nav__hum::before, .nav.active .nav__hum::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
  }
